    border-radius: var(--radius);
    background-image: linear-gradient(120deg, #fff 25%, rgba(255, 255, 255, 0) 50%), linear-gradient(300deg, #ccc 14%, rgba(255, 255, 255, 0) 40%);
    z-index: -1; }
