  box-shadow: 5px 5px 10px var(--shadow-color), 1px 1px 2px var(--shadow-color), -6px -6px 15px rgba(255, 255, 255, 0.9), -1px -1px 3px 1px rgba(255, 255, 255, 0.9);
  outline: 0;
  margin-left: 10px; }

/*# sourceMappingURL=PathCalculate.svelte.css.map */</style>

<div class="calculate-block">

  <div class="result-wrapper">
    <div class="constants">
      <label>
        <span>Population size:</span>
        <input
          class="text-input"
          bind:value={populationSize}
          type="number"
          step="1"
          min="1"
          max="50" />
      </label>
      <label>
        <span>Crossover probability:</span>
        <input
          class="text-input"
          bind:value={crossoverProbability}
          type="number"
          step="0.1"
          min="0.01"
          max="1" />
      </label>
      <label>
        <span>Mutation probability:</span>
        <input
          class="text-input"
          bind:value={mutationProbability}
          type="number"
          step="0.01"
          min="0.01"
          max="1" />
      </label>
      <label>
        <span>Interval duration:</span>
        <input
          class="text-input"
          bind:value={intervalDuration}
          type="number"
          step="20"
          min="10"
          max="3000" />
      </label>

      <div class="buttons">
        <button class="startButton protrude" on:click={onStart}>Start</button>
        <button class="startButton protrude" on:click={onStop}>Stop</button>
      </div>
    </div>

    <ResultGrid
      {running}
      {graph}
      {currentGeneration}
      {mutationsCount}
      {bestValue}
      {currentBest}
      {population}
      {best}
      {bestValuesArray} />
  </div>
</div>
